* {
	font-family: 'Roboto', Arial, sans-serif; }

body {
	min-width: 320px;
	color: $darkGray;
	background: $lightGray;
	font-family: 'Roboto', Arial, sans-serif;
	font-size: 14px;
	line-height: 1.3; }

html,
body,
#root {
	height: 100%; }

.out {
	display: flex;
	flex-direction: column;
	height: 100%; }

.container {
	overflow: auto;
	flex-grow: 1;
	height: 100%;
	&_simple {
		padding: 30px;
		@include xs {
			padding: 0;
			height: auto; } } }

.inner {
	padding: 40px 20px;
	margin: 0 auto;
	max-width: 1024px;
	width: 100%; }

a:not([class]) {
	color: $blue;
	text-decoration: none;
	@include hover {
		text-decoration: underline; } }
