html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-angle-down {
  width: 1.71em;
  height: 1em;
  fill: initial;
}

.icon-check {
  width: 1.3em;
  height: 1em;
  fill: initial;
}

* {
  font-family: 'Roboto', Arial, sans-serif;
}

body {
  min-width: 320px;
  color: #333;
  background: #f8f8f8;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 14px;
  line-height: 1.3;
}

html,
body,
#root {
  height: 100%;
}

.out {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.container {
  overflow: auto;
  flex-grow: 1;
  height: 100%;
}

.container_simple {
  padding: 30px;
}

@media only screen and (max-width: 599px) {
  .container_simple {
    padding: 0;
    height: auto;
  }
}

.inner {
  padding: 40px 20px;
  margin: 0 auto;
  max-width: 1024px;
  width: 100%;
}

a:not([class]) {
  color: #2e99db;
  text-decoration: none;
}

.no-touch a:not([class]):hover {
  text-decoration: underline;
}

.btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  padding: 0 15px;
  min-width: 120px;
  border: none;
  outline: none;
  color: #fff;
  background: #2e99db;
  font-size: 19px;
  font-weight: 300;
  line-height: 60px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: opacity .175s, background .175s, box-shadow .175s;
}

.btn:active {
  color: #fff;
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.btn_gray {
  background: #bec1cb;
}

.btn_gray:active {
  background: #a2a6b4;
}

.btn_red {
  background: #ec4a41;
}

.btn_red:active {
  background: #e32117;
}

.btn_wide {
  width: 100%;
}

.btn_sm {
  height: 44px;
  line-height: 44px;
}

.select {
  position: relative;
  text-align: left;
  user-select: none;
}

.select_top .select__body {
  top: auto;
  bottom: 100%;
  margin: 0 0 4px;
}

.select__header {
  position: relative;
  padding: 0 36px 0 12px;
  border: 1px solid #ccc;
  width: 100%;
  height: 60px;
  background: #fff;
  appearance: none;
  transition: border-color 0.25s;
  cursor: pointer;
  transition: border-color 0.25s;
}

.select.is-open .select__header {
  border-color: #2e99db;
}

.select.is-error .select__header {
  border-color: #ec4a41;
}

.select__value {
  position: relative;
  z-index: 2;
  height: 58px;
  font-size: 16px;
  font-weight: 400;
  line-height: 58px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.select__arrow {
  position: absolute;
  z-index: 1;
  top: 50%;
  right: 12px;
  fill: #ccc;
  font-size: 8px;
  margin-top: -0.4em;
  transition: transform 0.25s;
}

.select.is-open .select__arrow {
  transform: rotateX(180deg);
}

.select__body {
  position: absolute;
  z-index: 20;
  top: 100%;
  left: 0;
  right: 0;
  display: none;
  overflow: auto;
  padding: 10px 0;
  margin-top: 4px;
  border: 1px solid #ccc;
  max-height: 200px;
  background: #fff;
}

.select.is-open .select__body {
  display: block;
}

.select__option {
  padding: 5px 12px;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  transition: color 0.25s, background 0.25s;
}

.no-touch .select__option:hover {
  color: #2e99db;
  background: rgba(0, 0, 0, 0.05);
}

.select__option:active {
  background: rgba(0, 0, 0, 0.1) !important;
}

.select__option.is-active {
  color: #fff !important;
  background: #2e99db !important;
}

.select .checkbox {
  display: block;
}

.input {
  padding: 0 15px;
  border: 1px solid #ccc;
  border-radius: 0;
  width: 100%;
  height: 60px;
  background: #fff;
  box-shadow: none;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  appearance: none;
  transition: border-color 0.25s;
}

@media only screen and (max-width: 767px) {
  .input {
    height: 50px;
  }
}

.input::-ms-clear {
  display: none;
}

.input:focus {
  border-color: #2e99db;
}

.input:disabled {
  border-color: #b3b3b3;
  background: #f2f2f2;
  cursor: not-allowed;
}

.input.is-error {
  border-color: #ec4a41;
}

.input_sm {
  height: 40px;
}

@media only screen and (max-width: 767px) {
  .input_sm {
    height: 40px;
  }
}

.checkbox {
  position: relative;
  display: inline-block;
  vertical-align: top;
  min-width: 30px;
  min-height: 30px;
  user-select: none;
}

.no-touch .checkbox:hover .checkbox__title {
  color: #2e99db;
}

.checkbox input {
  display: none;
}

.checkbox input:checked ~ .checkbox__icon .icon {
  opacity: 1;
}

.checkbox input:disabled ~ .checkbox__icon, .checkbox input:disabled ~ .checkbox__title {
  opacity: 0.5;
  cursor: not-allowed;
}

.checkbox__icon {
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #ccc;
  border-radius: 3px;
  width: 30px;
  height: 30px;
  background: #fff;
  cursor: pointer;
  transition: border-color 0.25s;
}

.checkbox__icon .icon {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0;
  fill: #333;
  font-size: 14px;
  transform: translate3d(-50%, -50%, 0);
  transition: opacity 0.25s, background 0.25s;
}

.checkbox__label {
  display: inline-block;
  vertical-align: top;
  padding: 5px 0 5px 40px;
  cursor: pointer;
  transition: color 0.25s;
}

.form-group {
  position: relative;
}

.form-group:not(:last-child) {
  margin-bottom: 20px;
}

.form-group__label {
  display: block;
  margin-bottom: 8px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.3;
}

.form {
  margin: 0 auto;
}

.form_sm {
  max-width: 400px;
}

.form_md {
  max-width: 600px;
}

.form_box {
  padding: 30px;
  background: #fff;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

@media only screen and (max-width: 599px) {
  .form_box {
    padding: 0;
    background: none;
    box-shadow: none;
  }
}

.form__section:not(:last-child) {
  padding-bottom: 20px;
}

.form__footer {
  padding-top: 10px;
}

.form__links {
  display: flex;
  justify-content: space-between;
  padding: 20px 0 0;
  margin: -5px;
}

.form__link {
  display: inline-block;
  vertical-align: top;
  padding: 5px;
  color: #2e99db;
  font-size: 14px;
  text-decoration: none;
}

.no-touch .form__link:hover {
  text-decoration: underline;
}
